import { mapActions, mapGetters } from 'vuex';
import MapMarker from 'vue-material-design-icons/MapMarker';
import ShareVariant from 'vue-material-design-icons/ShareVariant';
import Plus from 'vue-material-design-icons/Plus';
import Pencil from 'vue-material-design-icons/Pencil';
import Delete from 'vue-material-design-icons/Delete';
import ImagePlus from 'vue-material-design-icons/ImagePlus';
import Close from 'vue-material-design-icons/Close';
import Download from 'vue-material-design-icons/Download';
import DownloadMultiple from 'vue-material-design-icons/DownloadMultiple';
import { NcActions, NcActionButton, NcButton, NcModal, NcEmptyContent, NcActionSeparator, NcLoadingIcon, isMobile } from '@nextcloud/vue';
import { getCurrentUser } from '@nextcloud/auth';
import { generateRemoteUrl } from '@nextcloud/router';
import FetchAlbumsMixin from '../mixins/FetchAlbumsMixin.js';
import FetchFilesMixin from '../mixins/FetchFilesMixin.js';
import AbortControllerMixin from '../mixins/AbortControllerMixin.js';
import HeaderNavigation from '../components/HeaderNavigation.vue';
import FilesPicker from '../components/FilesPicker.vue';
import CollectionContent from '../components/Collection/CollectionContent.vue';
import ActionFavorite from '../components/Actions/ActionFavorite.vue';
import ActionDownload from '../components/Actions/ActionDownload.vue';
import CollaboratorsSelectionForm from '../components/Albums/CollaboratorsSelectionForm.vue';
import AlbumForm from '../components/Albums/AlbumForm.vue';
import logger from '../services/logger.js';
import client from '../services/DavClient.js';
import DavRequest from '../services/DavRequest.js';
import { genFileInfo } from '../utils/fileUtils.js';
export default {
  name: 'AlbumContent',
  components: {
    MapMarker,
    ShareVariant,
    Plus,
    Pencil,
    Close,
    Delete,
    Download,
    DownloadMultiple,
    FilesPicker,
    HeaderNavigation,
    ImagePlus,
    NcEmptyContent,
    NcActions,
    NcActionButton,
    NcButton,
    NcModal,
    NcLoadingIcon,
    NcActionSeparator,
    CollectionContent,
    ActionFavorite,
    ActionDownload,
    AlbumForm,
    CollaboratorsSelectionForm
  },
  mixins: [AbortControllerMixin, FetchAlbumsMixin, FetchFilesMixin, AbortControllerMixin, isMobile],
  props: {
    albumName: {
      type: String,
      default: '/'
    }
  },

  data() {
    return {
      showAddPhotosModal: false,
      showManageCollaboratorView: false,
      showEditAlbumForm: false,
      loadingAddCollaborators: false
    };
  },

  computed: { ...mapGetters(['albumsFiles']),

    /**
     * @return {object} The album information for the current albumName.
     */
    album() {
      return this.albums[this.albumName] || {};
    },

    /**
     * @return {string[]} The list of files for the current albumName.
     */
    albumFileIds() {
      return this.albumsFiles[this.albumName] || [];
    },

    /**
     * @return {boolean} Whether sharing is enabled.
     */
    sharingEnabled() {
      return OC.Share !== undefined;
    }

  },
  watch: {
    album() {
      this.fetchAlbumContent();
    }

  },
  methods: { ...mapActions(['appendFiles', 'deleteAlbum', 'addFilesToAlbum', 'removeFilesFromAlbum', 'updateAlbum']),

    async fetchAlbumContent() {
      if (this.loadingFiles || this.showEditAlbumForm) {
        return [];
      }

      const semaphoreSymbol = await this.semaphore.acquire(() => 0, 'fetchFiles');
      const fetchSemaphoreSymbol = await this.fetchSemaphore.acquire();

      try {
        this.errorFetchingFiles = null;
        this.loadingFiles = true;
        this.semaphoreSymbol = semaphoreSymbol;
        const response = await client.getDirectoryContents(`/photos/${getCurrentUser()?.uid}/albums/${this.albumName}`, {
          data: DavRequest,
          details: true,
          signal: this.abortController.signal
        });
        const fetchedFiles = response.data.map(file => genFileInfo(file)) // For the Viewer.
        .map(file => ({ ...file,
          source: generateRemoteUrl(`dav${file.filename}`)
        }));
        const fileIds = fetchedFiles.map(file => file.fileid).map(fileId => fileId.toString());
        this.appendFiles(fetchedFiles);

        if (fetchedFiles.length > 0) {
          await this.$store.commit('addFilesToAlbum', {
            albumName: this.albumName,
            fileIdsToAdd: fileIds
          });
        }

        logger.debug(`[AlbumContent] Fetched ${fileIds.length} new files: `, fileIds);
      } catch (error) {
        if (error.response?.status === 404) {
          this.errorFetchingFiles = 404;
        } else if (error.code === 'ERR_CANCELED') {
          return;
        } else {
          this.errorFetchingFiles = error;
        } // cancelled request, moving on...


        logger.error('Error fetching album files');
        console.error(error);
      } finally {
        this.loadingFiles = false;
        this.semaphore.release(semaphoreSymbol);
        this.fetchSemaphore.release(fetchSemaphoreSymbol);
      }

      return [];
    },

    redirectToNewName(_ref) {
      let {
        album
      } = _ref;
      this.showEditAlbumForm = false;

      if (this.album.basename !== album.basename) {
        this.$router.push({
          name: 'albums',
          params: {
            path: album.basename
          }
        });
      }
    },

    async handleFilesPicked(fileIds) {
      this.showAddPhotosModal = false;
      await this.addFilesToAlbum({
        albumName: this.albumName,
        fileIdsToAdd: fileIds
      }); // Re-fetch album content to have the proper filenames.

      await this.fetchAlbumContent();
    },

    async handleRemoveFilesFromAlbum(fileIds) {
      this.$refs.collectionContent.onUncheckFiles(fileIds);
      await this.removeFilesFromAlbum({
        albumName: this.albumName,
        fileIdsToRemove: fileIds
      });
    },

    async handleDeleteAlbum() {
      await this.deleteAlbum({
        albumName: this.albumName
      });
      this.$router.push('/albums');
    },

    async handleSetCollaborators(collaborators) {
      try {
        this.loadingAddCollaborators = true;
        this.showManageCollaboratorView = false;
        await this.updateAlbum({
          albumName: this.albumName,
          properties: {
            collaborators
          }
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingAddCollaborators = false;
      }
    }

  }
};